.nav{
    margin-top: 5px;
    grid-area: n;
    background-color: rgb(162, 165, 165);
    border-radius: 10px;
    padding: 60px;
    text-align: center;
}
.item{
    margin: 10px auto;
    border: 1px solid rgb(34, 109, 247);
    border-radius: 5px;
    padding: 10px 50px;
   
    background-color:rgb(34, 222, 247) ;
}
.item a{
    color: black;
    text-decoration: none;
    font-size: 20px;
}
.item a.active{
    color: red;
    font-size: 24px;

}