.app-wrapper{
    margin: 0 auto;
    display: grid;
    width: 1200px;
    grid-template-areas: 
    "h h" 
    "n c"; 
    grid-template-rows:  60px 1fr;
    grid-template-columns: 2fr 10fr;
}
.app-wrapper-content{
    margin-left: 5px;
    margin-top: 5px;
    grid-area: c;
    background-color: rgb(127, 224, 219);
    border-radius: 10px;
}
